import path from "path";
import fs from "fs";
import { parse } from "csv-parse";
import { stringify } from "csv-stringify";
const getAllData = async () => {
  const filePath = path.resolve("2024-07-23.log");
  const fileContent = fs.readFileSync(filePath, "utf-8");
  return fileContent;
};
async function load() {
  return {
    status: 200,
    info: await getAllData()
  };
}
const actions = {
  callsign: async ({ request }) => {
    const formData = await request.formData();
    let id = formData.get("id");
    let date = formData.get("date");
    console.log(date);
    console.log(id);
    let filePath, fileContent;
    try {
      filePath = path.resolve(`${date}.log`);
      fileContent = fs.readFileSync(filePath, "utf-8");
    } catch {
      return {
        status: 404,
        info: `no such file ${date}.log`
      };
    }
    if (id == "") {
      return {
        status: 404,
        info: await getAllData()
      };
    }
    const records = await new Promise((resolve, reject) => {
      parse(
        fileContent,
        {
          columns: true,
          // Assuming the first row contains headers
          trim: true
        },
        (err, output) => {
          if (err) {
            reject(err);
          } else {
            resolve(output);
          }
        }
      );
    });
    id = id?.toString().toUpperCase();
    const procRecs = [];
    records.map((r) => {
      r["name"] == id ? procRecs.push(r) : {};
    });
    if (procRecs.length <= 0) {
      return {
        status: 404,
        info: `no data for ${id}`
      };
    }
    const telems = procRecs.map((r) => r["telemetry"]);
    const times = procRecs.map((r) => r["utime"]);
    const csvString = await new Promise((resolve, reject) => {
      stringify(
        procRecs,
        {
          header: true
          // Include headers in the output CSV
        },
        (err, output) => {
          if (err) {
            reject(err);
          } else {
            resolve(output);
          }
        }
      );
    });
    return {
      status: 200,
      info: csvString,
      id,
      telemetry: telems,
      times,
      date
    };
  }
};
export {
  actions,
  load
};
